@import '../common/styles/saas/variables';
@import '../common/styles/saas/mixins';

.mainBlock {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  padding: 150px 0;
  background-color: white;
  @include flex(row, center, center, nowrap);

  .particles {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0.2;
  }

  .container {
    @include container();
    @include flex(row, center, center, wrap);

    .intro {
      @include flex(column, flex-start, flex-start, nowrap);
      @include text($textFont, 18px, 400, 1.2);
      letter-spacing: 1.4px;
      @media screen and (max-width: 855px) {
        text-align: center;
      }
      @media screen and (max-width: 585px) {
        align-items: center;
        text-align: center;
      }

      h1 {
        color: $textColor;
        margin: 10px 0;
        font-family: $titleFont;
        font-size: 80px;
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: .2;
        z-index: -1;
        background-image: url('../assets/images/fotis-fotopoulos-LJ9KY8pIH3E-unsplash.jpg');
        background-position: center;
        background-size: cover;
      }
      :nth-child(3){
          text-transform: uppercase;
        color: $textColor;
      }
    }
  }
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.2;
}
